import Vue from 'vue'
import Vuex from 'vuex'
import questionPack from './modules/question_pack'
import questions from './modules/questions'
import test from './modules/test'
import axios from './axios'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
  state: {
    user: null
  },

  mutations: {
    setLoggedUser: (state, data) => { state.user = data }
  },

  actions: {
    getLoggedUser: ({commit}) => {
      let userID = window.$cookies.get('user_id')
      axios.get('/api/v1/user', { params: { id: userID } })
        .then(({data}) => {
          commit('setLoggedUser', data)
        })
    }
  },

  modules: {
    questionPack,
    questions,
    test
  },
  strict: debug
})



