<template>
  <b-navbar type="dark" variant="info" toggleable="md">
    <b-navbar-toggle target="nav_dropdown_collapse"></b-navbar-toggle>
    <b-navbar-brand :to="{ name: 'Home' }">TestManager</b-navbar-brand>

    <b-collapse is-nav id="nav_dropdown_collapse">
      <b-navbar-nav class="ml-auto">
        <b-nav-item :to="{ name: 'TestEditor' }">Редактор тестов</b-nav-item>
        <b-nav-item :to="{ name: 'QuestionEditor' }">Редактор вопросов</b-nav-item>
        <b-nav-item v-if="!checkAuthentication()"
                    :to="{ name: 'Authentication' }">Войти</b-nav-item>
        <div>
          <b-nav-item-dropdown :text='user.username'
                               v-if="checkAuthentication()"
                               boundary='window'>
            <b-dropdown-item @click="submitSignout()">Выйти</b-dropdown-item>
          </b-nav-item-dropdown>
        </div>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</template>

<script>
import Authentication from '@/components/pages/Authentication'
import { mapState } from 'vuex'

export default {
  computed: {
    ...mapState([ 'user' ])
  },

  mounted () {
    this.$store.dispatch('getLoggedUser')
  },

  methods: {
    submitSignout () {
      Authentication.signout(this, '/')
    },

    checkAuthentication () {
      return Authentication.user.authenticated
    }
  }
}
</script>



